* {
    box-sizing: border-box;
}

body {
    margin: 0;
}

.container {
    justify-content: center;
}

.nav {
    background-color: transparent;
    color: #282828;
    font-weight: bold;
    display: flex;
    width: 60%;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    padding: 0 1rem;
    margin-top: 10px;
}

.site-title {
    font-family: 'DM Serif Display', serif;
    font-size: 2rem;
}

.nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
}

.nav li {
    position: relative;
}

.nav a {
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
}

.nav a::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: transparent;
    bottom: 0;
    left: 0;
    transform: scaleX(0);
    transform-origin: 0 100%;
    transition: transform 0.3s ease;
}

.nav a:hover::before,
.nav li.active a::before {
    background-color: #555;
    transform: scaleX(1);
}